@keyframes germFloat {
  0% {
    transform: translate(0)rotate(0)scale(.8);
  }

  25% {
    transform: translate(10px, -15px)rotate(15deg)scale(1);
  }

  50% {
    transform: translate(-10px, 10px)rotate(-10deg)scale(1.1);
  }

  75% {
    transform: translate(5px, -5px)rotate(5deg)scale(.9);
  }

  100% {
    transform: translate(0)rotate(0)scale(.8);
  }
}

@keyframes gentlePulse {
  0%, 100% {
    transform: scale(1);
  }

  50% {
    transform: scale(1.02);
  }
}

@keyframes slideIn {
  from {
    opacity: 0;
    transform: translateX(-20px);
  }

  to {
    opacity: 1;
    transform: translateX(0);
  }
}

@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(20px);
  }

  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.news-container {
  flex-wrap: wrap;
  justify-content: center;
  gap: 20px;
  padding: 20px;
  display: flex;
}

.news-article {
  background: #fff;
  border: 1px solid #ddd;
  border-radius: 10px;
  flex-direction: column;
  flex: calc(25% - 40px);
  justify-content: space-between;
  max-width: 250px;
  transition: transform .3s ease-in-out, box-shadow .3s ease-in-out;
  animation: .5s ease-in-out fadeIn;
  display: flex;
  overflow: hidden;
  box-shadow: 2px 2px 10px #0000001a;
}

.news-article:hover {
  transform: scale(1.05);
  box-shadow: 4px 4px 15px #0003;
}

.news-article img {
  object-fit: cover;
  width: 100%;
  height: 120px;
}

.news-content {
  flex-direction: column;
  flex-grow: 1;
  justify-content: space-between;
  padding: 10px;
  display: flex;
}

.news-content h3 {
  text-overflow: ellipsis;
  white-space: nowrap;
  height: 40px;
  margin: 0 0 8px;
  font-size: 14px;
  overflow: hidden;
}

.news-content p {
  color: #555;
  text-overflow: ellipsis;
  height: 40px;
  margin: 0 0 5px;
  font-size: 12px;
  overflow: hidden;
}

.news-content a {
  color: #007bff;
  font-size: 12px;
  text-decoration: none;
}

.news-content a:hover {
  text-decoration: underline;
}

.error {
  color: red;
  text-align: center;
}

@media (width <= 768px) {
  .news-article {
    flex: calc(50% - 40px);
  }
}

@media (width <= 480px) {
  .news-article {
    flex: 100%;
  }
}

header {
  color: #fff;
  z-index: 1000;
  background-color: #1e3e62;
  width: 100%;
  padding: 1rem;
  position: fixed;
  top: 0;
}

header nav {
  justify-content: space-between;
  align-items: center;
  display: flex;
}

header nav .menu-icon {
  cursor: pointer;
  font-size: 1.5rem;
  display: none;
}

@media (width <= 768px) {
  header nav .menu-icon {
    display: block;
  }
}

header nav .nav-links {
  gap: 1.5rem;
  display: flex;
}

@media (width <= 768px) {
  header nav .nav-links {
    text-align: center;
    background: #1e3e62;
    flex-direction: column;
    width: 100%;
    padding: 1rem;
    display: none;
    position: absolute;
    top: 60px;
    right: 0;
  }

  header nav .nav-links.active {
    display: flex;
  }
}

header nav .nav-links a {
  color: #fff;
  font-size: 1.1rem;
  text-decoration: none;
  transition: color .3s;
}

header nav .nav-links a:hover {
  color: #ff6500;
}

footer {
  color: #fff;
  text-align: center;
  background-color: #1e3e62;
  margin-top: 2rem;
  padding: 1rem;
}
